<template>
  <div id="sign-in">
    <head-top title="签到"></head-top>
    <div class="bg"></div>
    <div class="content">
      <div class="success-info">{{$store.getters.student["StudentName"] + '，' + state}}</div>
      <!-- <div class="sub-title">2018高级英语-托福雅思的考点解析</div> -->
      <div class="title">{{course["CourseName"]}}</div>
      <div class="tc-grade">
        <span class="tc-name">老师：{{course["TeacherName"]}}</span>
        <span class="grade">班级：{{course["ClassName"]}}</span>
      </div>
      <div class="croom-Time">
        <span class="c-room">教室：{{course["ClassroomName"]}}</span>
        <span class="time">
          时间：
          {{course['StartTime'] | formatTime(course['StartTime']) }}
          -
          {{course['EndTime'] | formatTime(course['EndTime']) }}
          <!-- {{nowDateTime?nowDateTime:(course["Key"]? formatDate(course["Key"]["SignTime"]) :"")}} -->
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";

// import { formatDate } from "@/utils/formatData";
// import { getNowDateTime } from "@/utils/index";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "",
  components: {
    headTop
  },
  props: {},
  data() {
    return {
      state: "签到中...",
      nowDateTime: "",
      course: this.$store.getters.course
    };
  },
  created() {
    window.acceptQRcode = code => {
      console.log("接收签到码：", code);
      this.qrCodeSign(code);
      return true;
    };
  },
  mounted() {
    this.$nextTick(() => {
      if (this.$route.params.state === "1") {
        this.state = "已签到";
      } else {
        if (this.$route.params.way === "1") {
          this.qrCodeSign("");
        } else {
          sendToAPP(FuncType.openQRcode, "");
        }
      }
    });
  },
  methods: {
    // signIn() {
    //   this.state = "签到中...";
    //   this.$gatewayHttp.sign
    //     .getInfo({
    //     courseID: this.$store.getters.course["CourseID"],
    //     studentID: this.$store.getters.studentID
    //   })
    //     .then(res => {
    //       if (res.Result) {
    //         this.state = "签到成功";
    //         if (res.Datas && res.Datas.length > 0) {
    //           this.course = res.Datas[0];
    //         }
    //       } else {
    //         this.state = "签到失败";
    //       }
    //     })
    //     .catch(err => {
    //       this.state = "签到失败";
    //     });
    // },
    qrCodeSign(qrCodeStr) {
      this.state = "签到中...";
      let reqData = {
        courseID: this.$store.getters.course["CourseID"],
        studentID: this.$store.getters.studentID,
        qrCodeStr:
          typeof qrCodeStr === "string" ? qrCodeStr : JSON.stringify(qrCodeStr)
      };
      this.$gatewayHttp.sign
        .getSign(reqData)
        .then(res => {
          if (res.Result) {
            this.state = "签到成功";
            this.course = {
              ...this.$store.getters.course
            };
            // this.nowDateTime = getNowDateTime().substring(0, 16);
          } else {
            this.state = res.Info ? res.Info : "签到失败";
          }
        })
        .catch(err => {
          this.state = "签到失败";
        });
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
#sign-in {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .bg {
    width: 100%;
    height: 27vh;
    background: url("../../../assets/qiandaobg.png");
    background-size: cover;
    margin-bottom: 48px;
  }
  .content {
    width: 85vw;
    background: rgba(255, 255, 255, 1);
    margin: 0 auto;
    overflow: hidden;
    padding-bottom: 1.5rem;
    .success-info {
      font-size: 2.5rem;
      margin-top: 2rem;
      line-height: 2;
      color: rgba(0, 203, 171, 1);
    }
    .title {
      font-size: 1.6rem;
      font-family: MicrosoftYaHei;
      line-height: 2.5;
    }
    .tc-grade,
    .croom-Time {
      text-align: left;
      box-sizing: border-box;
      font-size: 1.25rem;
      line-height: 3;
      color: #666;
      span {
        &:nth-child(1) {
          width: 32%;
          padding-left: 8%;
          margin-left: 1.2rem;

          display: inline-block;
        }
        &:nth-child(2) {
          width: 50%;
          margin-left: 1.2rem;
        }
      }
    }
  }
}
</style>


